<template>
<div class="login">
  <div class="login-wrap">
  <a-form-model
  layout='horizontal'
  :model="form"
  :label-col="labelCol"
  :wrapper-col="wrapperCol"
  :rules="rules"
  ref="form"
  >
    <a-form-model-item label="" prop="UsernameOrEmailAddress">
      <a-input v-model="form.UsernameOrEmailAddress" placeholder="" />
    </a-form-model-item>
    <a-form-model-item label="" prop="Password">
      <a-input v-model="form.Password" placeholder="" />
    </a-form-model-item>
    <a-form-model-item label="">
      <a-checkbox v-model="form.RememberMe">
        
      </a-checkbox>
      <a-button type="primary" style="float: right;width:100px;" @click="login">
        
      </a-button>
    </a-form-model-item>
  </a-form-model>
  </div>
</div>
</template>
<script>
import { login } from './service'
export default{
  methods:{
    login(){
      let _this = this;
      this.$refs.form.validate(valid => {
        if (valid) {
          login(this.form).then(res => {
            _this.$router.push('/home')
          })
        }
      });
    },
    goTo(){
      this.$router.push('/home')
    }
  },
  data(){
    return {
      labelCol: { span: 0 },
      wrapperCol: { span: 24 },
      form:{

      },
      rules:{
        UsernameOrEmailAddress:[{ required: true, message: '', trigger: 'blur' }],
        Password:[{ required: true, message: '', trigger: 'blur' }]
      }
    }
  }
}
</script>
<style lang="less" scoped>
.login-wrap{
  width: 400px;
  background: #fff;
  padding:15px;
  margin:100px auto 15px;
}
</style>